* {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  font-weight: 500;
  color: var(--black);
  scroll-behavior: smooth;
  /* Add scroll-padding magic to fix Nav bar overlap issues on local hyperlinks. */
  scroll-padding-top: 6.75rem;
}

body {
  background-image: url(../images/cc_website_background.png);
  font-family: var(--font_family_1);
  line-height: 1.15;
}

main {
  width: 100vw;
  margin-top: 15px;
  padding-bottom: 25px;
  background: var(--white);
  border-top: double 8px var(--brand_color_1);
  border-bottom: double 8px var(--brand_color_1);
  overflow: hidden;
}

/* #region Base Hyperlinks */

/* Remember that when setting styles for link states, 
          a:hover must be declared after a:link & a:visited and 
          a:active must be declared after a:hover. */

a {
  text-decoration: none;
}

a:active,
a:hover,
a:link,
a:visited {
  color: var(--brand_color_1);
  font-weight: 700;
}

a:hover {
  text-decoration: underline double #dd137b 1px;
  color: var(--brand_color_1);
}

a:visited {
  color: var(--brand_color_2);
}
a:active {
  color: var(--black);
}

/* #endregion */
