.strong {
  font-weight: 700;
}

.emphasis {
  margin: 0;
  padding: 0;
  font-style: italic;
}

.center {
  text-align: center;
}

.lighten {
  filter: brightness(125%);
}

.darken {
  filter: brightness(75%);
}

/* Turns out this is as simple as setting display to ...inline-block.  */
span.clean_break_on_wrap {
  display: inline-block;
}
